import useNotification from "../custom-hooks/use-notification";
import TopBar from "../Components/TopBar";
import Menu from "../Components/Menu";
import Notification from "../UI/Notification";
import Footer from "../Components/Footer";

const FAQ = () => {
  const { notification: copyNotification, onNoti: onCopy } = useNotification();

  return (
    <>
      <header>
        <TopBar copy={onCopy} />
        <Menu />
      </header>
      <Notification onShow={copyNotification} text={"Skopiowano do schowka"} />
      <h1 className="section-title">
        Czsto zadawane <span className="text-orange">pytania</span>
      </h1>
      <section className="w-[90vw] lg:w-[70vw] mb-[50px] customMargin md:mb-[50px]">
        <h3 className="mb-[10px] text-[25px] md:text-[30px] ">
          1. Co jest potrzebne do rozpoczcia kursu jazdy ?
        </h3>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Przed rozpoczciem kursu jazdy nazley wyrobi
          <span className="text-orange">
            {" "}
            Profil Kandydata na Kierowc(PPK)
          </span>
          . Profil daje moliwo szkolenia w dowolnej szkole nauki jazdy i
          zdawania egzaminw pastwowych na terenie caej Polski.
        </p>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          2. Gdzie wyrobi Profil Kandydata na Kierowc ?
        </h3>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Profil Kandydata na Kierowce mona wyrobi w placwkach Urzdu Miasta
          lub Gminy. Istnieje rwnie moliwo wyrobienia PPK{" "}
          <span className="text-orange">online</span>.
        </p>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          3. Co jest potrzebne do wyrobienia PPK ?
        </h3>
        <ul>
          <li className="mb-[10px] text-[16px] md:text-[18px]">
            - dowd osobisty/ paszport/ legitymacja szkolna
          </li>
          <li className="mb-[10px] text-[16px] md:text-[18px]">
            - wniosek o wydanie prawa jazdy
          </li>
          <li className="mb-[10px] text-[16px] md:text-[18px]">
            - pisemna zgoda rodzicw, dla osb niepenoletnich
          </li>
          <li className="mb-[10px] text-[16px] md:text-[18px]">
            - orzeczenie lekarskie wydane przez lekarza uprawnionego do badania
            kierowcw na kategori, na ktr zamierzamy rozpocz kurs
          </li>
          <li className="mb-[10px] text-[16px] md:text-[18px]">
            - zdjcie 3,5cm x 4,5cm
          </li>
        </ul>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          4. W jakim wieku mona rozpocz kurs nauki jazdy kategorii B ?
        </h3>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Dla kategorii B minimalny wiek to
          <span className="text-orange"> 17 lat i 9 miesicy</span>
        </p>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          5. Ile Trwa kurs prawa jazdy? Ile trwaj wykady, a ile jazdy?
        </h3>
        <ul>
          <li className="mb-[10px] text-[16px] md:text-[18px]">
            Kursy <span className="text-orange">weekendowe</span> trwaj
            rednio, kada lekcja teorii ma 45 minut, terminy i dugo jazd s
            ustalane indywiduanie.
          </li>
          <li className="mb-[10px] text-[16px] md:text-[18px]">
            Kursy <span className="text-orange">ekspresowe</span> trwaj 18 dni.
          </li>
        </ul>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          6. Jak przebiega nauka teorii?
        </h3>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Na kadym 45 minutowym wykadzie, omawiamy zasady ruchu drogowego, na
          podstawie filmw, plansz oraz czynnej dyskusji, uczymy rwnie
          pierwszej pomocy. Po zakoczeniu teoretycznej czci kursu
          organizujemy <span className="text-orange">egzaminy wewntrzne</span>.
        </p>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          7. Jak przebiegaj jazdy ?
        </h3>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          W trakcie jazd zwracamy szczegln uwag na{" "}
          <span className="text-orange">bezpieczestwo</span>. Staramy si
          rwnie nie wywiera <span className="text-orange">presji</span> na
          kursantach. Nasze jazdy przebiegaj w spokojnej, miej atmosferze. Z
          nami bez problemowo opanujesz wszystkie manewry potrzebne do zdania
          egzaminu i bdziesz potrafi jezdzi samochodem.
        </p>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          8. Czy mona dokupi dodatkowe godziny po zakoczeniu kursu ?
        </h3>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Tak, oferujemy jazdy doszkalajce. Kosztuj{" "}
          <span className="text-orange">80</span> zotych za godzin.
        </p>
        <h3 className="mb-[10px] text-[25px] md:text-[30px]">
          9. Gdzie zrobi badania lekarskie potrzebne do wyrobienia PPK ?
        </h3>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Badania lekarskie dla kandydatw na kierowcw: Lek.med. Tomasz
          Zwinczak ul. Majora Henryka Sucharskiego 6 Dr Teresa Le ul. Ksidza
          Jerzego Popieuszki 42a
        </p>
      </section>
      <Footer copy={onCopy} />
    </>
  );
};

export default FAQ;
