import useNotification from "../custom-hooks/use-notification";
import TopBar from "../Components/TopBar";
import Menu from "../Components/Menu";
import Notification from "../UI/Notification";
import Footer from "../Components/Footer";

const UselessPage = () => {
  const { notification: copyNotification, onNoti: onCopy } = useNotification();

  return (
    <>
      <header>
        <TopBar copy={onCopy} />
        <Menu />
      </header>
      <Notification onShow={copyNotification} text={"Skopiowano do schowka"} />
      <h1 className="section-title">
        Dekalog <span className="text-orange">EkoKierowcy</span>
      </h1>
      <section className="w-[90vw] lg:w-[70vw] mb-50[px] customMargin md:mb-[50px]">
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Bd wiadomym ekokierowc
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Kontroluj spalanie. Tylko regularna kontrola poziomu spalania pozwoli
          Ci z jednej strony doskonali swj sposb jazdy, a z drugiej szybko
          zorientowa si, gdy co zaczyna szwankowa w samochodzie. Postaraj
          si zapisywa swoje rednie spalanie oraz rzuca sobie wyzwania
          obnienia go. Zmniejszenie spalania paliwa o 1,5 litra na 100 km.
          pozwoli Ci zaoszczdzi okoo 180 litrw paliwa rocznie (przy
          szacowanym przebiegu 12000 km) to okoo 720 z. Wiesz ju na co je
          wydasz ?
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Nie szaruj
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Najbardziej ekonomiczna jest prdko staa na jak najwyszym biegu
          przy maksymalnie 2500 obrotw na minut (samochody benzynowe).
          Generalnie mona stwierdzi, e jest to okoo 80-100 km/h. Przy
          wysokich prdkociach due znaczenie pod wzgldem spalania ma opr
          aerodynamiczny. Opr jest proporcjonalny do kwadratu prdkoci, co
          oznacza, dwukrotny wzrost prdkoci powoduje czterokrotny wzrost oporu
          powietrza. Szacuje si, e jadc z prdkoci 120 km/h zuywa si 30%
          wicej paliwa ni jadc z prdkoci 80 km/h. Jadc z prdkoci 140
          km/h zuywa si o 20% wicej paliwa ni jadc z prdkoci 120 km/h.
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Nie dociaj samochodu (rwnie aerodynamicznie)
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Kade dodatkowe 10 kg w samochodzie to nie mniej ni 0,1 litra na 100
          km. Zastanw si wic czy jest sens wozi przez cay rok acuchy
          zimowe, opat do niegu, stare gazety, skrzynk butelek po piwie, czy
          cokolwiek innego tam wozisz. Nie wo tego czego na pewno nie bdziesz
          potrzebowa. Pamitaj, e wszystkie dodatkowe elementy, ktre
          domontujesz do samochodu, takie jak baganiki dachowe, rowerowe, due
          anteny, a nawet zawieszone na nich flagi negatywnie wpyn na zuycie
          paliwa z uwagi na zwikszanie oporu powietrza.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px]">
          Szacuje si, e baganik dachowy przy prdkoci 120 km/h zwikszy
          zuycie paliwa o okoo 20%. Podobnie zadziaaj otwarte szyby przy
          wikszej prdkoci. Zrezygnuj rwnie z owiewek okiennych, wbrew
          pozorom negatywnie wpywaj one na opr powietrza z uwagi na
          powikszanie powierzchni czoowej auta. (Oczywicie pamitaj, e przy
          wyszych prdkociach jedzimy z zamknitymi szybami, nawet kosztem
          wczenia klimatyzacji).
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Utrzymuj samochd w dobrym stanie
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Pamitaj o tym, e stan techniczny Twojego samochodu znaczco wpywa
          na zuycie paliwa. Pamitaj o regularnej wymianie filtrw  paliwa i
          powietrza. Dbaj o waciw geometri zawieszenia. Pamitaj o
          regularnym (przynajmniej raz w miesicu!) sprawdzaniu cinienia w
          oponach.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Spadek cinienia w oponach o 25% zwiksza opr toczenia o 10%, co
          przekada si na 2% wzrost zuycia paliwa. Wikszo z nas jedzi na
          niedopompowanych oponach, tymczasem weryfikacja i doprowadzenie do
          stanu idealnego moe zosta wykonana na wikszoci stacji paliw za
          darmo.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Pamitaj rwnie o tym, e rwnie rodzaj opon wpywa na opory
          toczenia oraz zuycie paliwa. Generaln zasad jest, e szersze,
          bardziej sportowe opony przeo si na wzrost zuycia paliwa.
          Wspczynnik toczenia podawany jest przez producentw wikszoci opon
          dostpnych na rynku.
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Unikaj gwatownego ruszania i hamowania
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Gwatowne ruszanie i hamowanie znacznie wpywa na Twoje wydatki na
          stacji benzynowej. Mdrzejsze posugiwanie si pedaem gazu i hamulca
          moe zmniejszy zuycie paliwa nawet do 25%. Kilka sekund jazdy na
          najwyszych obrotach moe rwna si nawet kilku minutom spokojnego
          naciskania na peda gazu.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">Pamitaj:</p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          1) rozpdzanie pojazdu zuywa duo wicej paliwa ni utrzymywanie
          staej prdkoci. Do rwnomiernej jazdy z prdkoci 50km/h samochd
          wykorzystuje jedynie 5kW mocy (25 kW przy prdkoci 120 km/h)
          pozostaa moc silnika wykorzystywana jest tylko podczas przyspieszania
          oraz jazdy z du prdkoci.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          2) Przyspieszenie ze startu zatrzymanego wymaga o 20% wicej energii
          ni w przypadku przyspieszania choby ledwo toczcego si samochodu.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          3) I bieg suy do ruszania. Nie przejedaj na nim wicej ni 2
          dugoci samochodu, gdy jest najmniej efektywny pod wzgldem stosunku
          wysokoci obrotw silnika do iloci przejechanych metrw.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          4) Jed samochodem tak jakby jecha rowerem. Jadc rowerem szanujesz
          prac swoich mini. Widzc przed sob czerwone wiato lub inn
          przeszkod nie przyspieszasz, aby znale si przy niej wczeniej i
          gwatownie zahamowa, lecz spokojnie do niej dojedasz najchtniej
          wykorzystujc swobodne toczenie si. Pomyl, e obroty silnika s jak
          Twoje pedaowanie na rowerze, to naprawd duo zmienia.
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Zmieniaj bieg na wyszy
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Samochody konstruuje si tak, by nisze biegi zapewniay najwiksz
          moc i pozwalay na lepsze przyspieszenia, jednak oznacza to take
          najwysze zuycie paliwa przez silnik samochodu. Poruszajc si na
          najwyszych biegach, pozwalamy silnikowi zej na nisze obroty, ktre
          nie tylko oszczdzaj paliwo, ale take mniej zuywaj jednostk
          napdow oraz pozwalaj na zmniejszenie emisji CO2.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          W przypadku samochodw na benzyn (oraz gaz) najbardziej optymalnie
          wykorzystujemy prac silnika zmieniajc bieg na wyszy przy
          osigniciu maksymalnie 2500 RPM, w przypadku silnikw diesla zaleca
          si zmian biegu na wyszy przy osigniciu maksymalnie 2000 RPM.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Oczywicie zalecane wartoci s podane orientacyjnie i chocia
          zazwyczaj nie odbiegaj znacznie to s zalene od specyfiki
          konkretnego silnika, czy te warunkw drogowych. Szczegowe zalecenia
          zazwyczaj znajduj si w instrukcji obsugi pojazdu.
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Wycz silnik, gdy czekasz
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          W zasadzie przy kadym postoju duszym ni 30 sekund bardziej
          ekonomicznym rozwizaniem jest zgasi silnik, ni czeka przy
          wczonym. Oczywicie rzadko powinnimy to robi w zwykym ruchu
          miejskim, ale ju podczas oczekiwania na przejazd pocigu, lub te
          czekajc na pasaera warto jest zgasi silnik. Silnik pracujcy na
          biegu jaowym przez 3 minuty zuyje ilo paliwa wystarczajc na
          przejechanie 1 km z prdkoci okoo 50 km/h Przy odpalaniu samochodu
          nie wciskaj pedau gazu! Silniki konstruowane s w taki sposb aby
          odpalay bez koniecznoci wcinicia pedau gazu. Nie zuywaj wic
          niepotrzebnie paliwa, tym bardziej, e wcinicie gazu prdzej zakci
          prac systemu paliwowego ni mu pomoe. Powizane gdy odpalisz to jed
          nie grzej silnika na luzie.
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Hamuj silnikiem, nie jed na luzie
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Biorc pod uwag popularno jazdy na luzie na polskich drogach wci
          tkwimy w latach 70-tych lub maksymalnie 80-tych. Tymczasem znakomita
          wikszo samochodw produkowanych po roku 1990-tym to samochody
          wyposaone we wtrysk paliwa oraz odcicie dopywu paliwa w trakcie
          hamowania silnikiem (cakowite zwolnienie pedau gazu i jazda na
          biegu). Oznacza to tyle, e w trakcie hamowania silnikiem spalanie
          wynosi ZERO! Dzieje si tak a do momentu w ktrym silnik osignie
          okoo 1300 obrotw. Wwczas paliwo na nowo zaczyna pyn do silnika
          aby zapobiec jego zganiciu. Idealnym wic sposobem jazdy jest
          przewidywanie sytuacji na drodze i zwalnianie pedau gazu w
          odpowiednim momencie. Np. podczas dojedania do czerwonego wiata
          dobrze jest zawczasu zdj nog z gazu i stopniowo redukowa biegi,
          gdy obroty silnika dochodz do 1300. Oszczdno wynika std, e
          dopyw paliwa jest odcinany od silnika i porusza si on bdc
          napdzany przez koa. Nie dzieje si tak podczas jazdy na luzie.
          Silnik nie jest wwczas napdzany przez koa i niepotrzebnie zuywa
          paliwo aby utrzymywa swj ruch. Hamowanie silnikiem przyczyni si
          rwnie do wikszych oszczdnoci na elementach ukadu hamulcowego
          takich jak klocki, czy te tarcze.
        </p>
        <h2 className="mb-[20px] text-[25px] md:text-[30px]  font-medium">
          Mdrze korzystaj z klimatyzacji i ogrzewania
        </h2>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Wiele osb zdaje sobie spraw z tego, e klimatyzacja zuywa paliwo,
          redukujc zasig samochodu nawet o 20%, jednak niewiele osb zdaje
          sobie spraw z tego, e jadc z otwartymi szybami zwikszamy opr
          powietrza i rwnie podnosimy poziom spalania.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Generaln zasad jest, e przy jedzie do prdkoci 50 km/h bardziej
          ekonomicznym rozwizaniem jest otworzenie okien, natomiast powyej
          prdkoci 50 km/h bardziej ekonomiczne jest wczenie klimatyzacji.
          Warto take pamita, eby podczas najwikszych letnich upaw, jadc
          z klimatyzacj wczy zamknity obieg powietrza, gdy wtedy
          klimatyzacja oszczdza energi schadzajc ju raz schodzone
          powietrze, a nie gorce pobierane z zewntrz.
        </p>
        <p className="mb-[20px] text-[16px] md:text-[18px] ">
          Zim, gdy wsiadasz do zimnego samochodu przejed pierwsze par
          kilometrw z wyczonym ogrzewaniem. Pozwoli to silnikowi szybciej
          osign odpowiedni temperatur, a Ty waciwie i tak nie odczujesz
          rnicy.
        </p>
      </section>
      <Footer copy={onCopy} />
    </>
  );
};

export default UselessPage;
